<template>
  <!-- 退款信息 -->
  <div class="footer">
    <div class="emp_top_box">
        <div class="emp_left">
          <h3>退款记录</h3>
          <div class="middle">
            <small class="middle_first">总退款金额：￥500.00。</small>
          </div>
        </div>
        <div class="btn_right">
          <el-button @click="newbtn()" type="primary" size="mini"
            >新建</el-button
          >
        </div>
      </div>
    <div class="power_main">
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        size="mini"
        :height="$store.state.innerheight - 600"
        :header-cell-style="{ background: '#f5f5f5' }"
      >
        <el-table-column prop="business" label="退款主题" width="">
        </el-table-column>
        <el-table-column prop="customer" label="退款原因" width="">
        </el-table-column>
        <el-table-column prop="business_order" label="退款金额" width="">
        </el-table-column>
        <el-table-column prop="business_order" label="退款人" width="">
        </el-table-column>
        <el-table-column prop="business_order" label="退款日期" width="">
        </el-table-column>
        <el-table-column prop="business_order" label="业务类型" width="">
        </el-table-column>
        <el-table-column label="操作" width="">
          <template #default="scope">
            <el-button @click="handleEdit(scope.row)" type="text" size="mini"
              >编辑</el-button
            >
            <el-button
              style="color: red"
              type="text"
              size="mini"
              @click="handleDel(scope.row)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
import { reactive, toRefs } from "vue";
import { getCurrentInstance } from "@vue/runtime-core";
export default {
  components: {},
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      reporterlist: [],
      rolelist: [],
      reporterId: "",
      report_number: "",
      report_subject: "",
      report_date: "",
      job_content: "",
      remarks: "",
      tableData: [
        {
          business: "跟进-电话",
          customer: "客户",
          business_order: "跟进",
        },
      ],
    });
    const newbtn = () => {
      proxy.$router.push({
        path:"/refund_details",
        query:{
          isedit:0,
          isouter:1
        }
      });
    };
    const handleEdit = () => {
      proxy.$router.push({
        path:"/refund_details",
        query:{
          isedit:1,
          isouter:1
        }
      });
    };
    const handleDel = () => {
      console.log();
    };
    return {
      ...toRefs(state),
      handleEdit,
      handleDel,
      newbtn,
    };
  },
};
</script>
