.footer {
  border: 1px solid #e0e0e0;
  width: 100%;
   padding:0 10px;
  box-sizing: border-box;
}
.emp_top_box {
  display: flex;
  align-items: center;
  justify-content: space-between;
   margin: -7px 0;
}
.emp_left {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.middle {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.middle > small {
  font-size: 13px;
}
.middle_first {
  padding-left: 30px;
  box-sizing: border-box;
}
.return_count {
