<template>
  <!-- 回款信息列表页 -->
  <div>
    <div class="cus_info_box">
      <div
        class="info_box"
        :style="{
          height: $store.state.innerheight - 130 + 'px',
          'overflow-y': 'auto',
        }"
      >
        <div class="basic_info_box">
          <div class="info_three">
            <div class="basic_two">
              <h3>回款计划</h3>
              <div class="basic_one">
                <small>总计划金额：￥5,000.00 ; 总完成金额：￥1000.00。</small>
              </div>
            </div>

            <div class="btn_right">
              <el-button @click="onSubmit()" type="primary" size="mini"
                >新建</el-button
              >
            </div>
          </div>
          <div class="info_emp_box">
            <el-table
              :height="300"
              size="mini"
              :data="tableData"
              border
              style="width: 100%"
              :header-cell-style="{ background: '#f5f5f5' }"
            >
              <el-table-column prop="Scheduling" label="排期" />
              <el-table-column prop="coll_title" label="回款计划主题" />
              <el-table-column prop="plan_moneny" label="回款计划金额" />
              <el-table-column prop="setday" label="设定回款日期" />
              <el-table-column prop="planstatu" label="计划状态" />

              <el-table-column label="操作" width="200">
                <template #default="scope">
                  <el-button size="mini" type="text">设为完成</el-button>
                  <el-button size="mini" type="text" @click="handedit(scope.row)">编辑</el-button>
                  <el-button size="mini" type="text" style="color: red"
                    >删除</el-button
                  >
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
        <div class="basic_info_box_two">
          <div class="info_three">
            <div class="basic_two">
              <h3>回款记录</h3>
              <div class="basic_one">
                <small>总回款金额：￥5,000.00 。</small>
              </div>
            </div>

            <div class="btn_right">
              <el-button @click="onSubmitone()" type="primary" size="mini"
                >新建</el-button
              >
            </div>
          </div>
          <div class="info_emp_box">
            <el-table
              :height="300"
              size="mini"
              :data="tableDatater"
              border
              style="width: 100%"
              :header-cell-style="{ background: '#f5f5f5' }"
            >
              <el-table-column prop="coll_title" label="回款主题" />
              <el-table-column prop="plan_moneny" label="回款金额" />
              <el-table-column prop="Payee" label="收款人" />
              <el-table-column prop="coll_date" label="收款日期" />
              <el-table-column prop="Businesstype" label="业务类型" />
              <el-table-column fixed="right" label="操作" width="200">
                <template #default="scope">
                  <el-button size="mini" type="text" @click="handeditone(scope.row)">编辑</el-button>
                  <el-button size="mini" type="text" style="color: red"
                    >删除</el-button
                  >
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
      </div>
    </div>
    <collectiondetail ref="collectiondetail"></collectiondetail>
  </div>
</template>

<script>
import { getCurrentInstance, reactive, toRefs } from "vue";
import collectiondetail from "./collectiondetail.vue";
export default {
  components: {
    collectiondetail,
  },
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      tableData: [
        {
          Scheduling: "ads",
          coll_title: "dddddd",
          plan_moneny: "ffff",
          setday: "bbb",
          planstatu: "kkkk",
        },
      ],
      tableDatater: [
        {
          coll_title: "dcas",
          plan_moneny: "vvv",
          Payee: "saaaaaaa",
          coll_date: "ggg",
          Businesstype: "pppp",
        },
      ],
    });
    const onSubmit = () => {
      proxy.$refs.collectiondetail.setshow();
    };
      const handedit = (res) => {
      proxy.$refs.collectiondetail.setedit(res);
    };
     const onSubmitone = () => {
     proxy.$router.push({
        path: "/Collection_new",
        query: {
          obj: {},
          isedit: 0,
          isouter:1
        },
      });
    };
    const handeditone = () => {
     proxy.$router.push({
        path: "/Collection_new",
        query: {
          obj: {},
          isedit: 0,
          isouter:1
        },
      });
    };
    return {
      ...toRefs(state),
      onSubmit,
      handedit,
      onSubmitone,
      handeditone
    };
  },
};
</script>

<style  scoped>
img {
  width: 20px;
  height: 20px;
}
h3 {
  padding: 0;
  margin: 0;
}
.basic_info_box {
  border: 1px solid #f5f5f5;
  padding: 10px;
  box-sizing: border-box;
}
.basic_info_box_two{
  border: 1px solid #f5f5f5;
  padding: 10px;
  box-sizing: border-box;
  margin-top: 10px;
}
.info_three {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}
.basic_two {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.basic_one {
  margin-left: 30px;
}

.middle {
  display: flex;
  align-items: center;
  margin: 20px 0 10px 0;
}
.basic_one > small {
  font-size: 13px;
}

</style>