<template>
 <!-- 点击编辑进入合同管理编辑界面 -->
    <div class="con_box">
         <el-tabs class="eltabs" v-model="activeName" @tab-click="handleClick">
            <el-tab-pane class="eltabpane" label="合同信息" name="con_info">
                <coninfo ref="coninfo"></coninfo>
            </el-tab-pane>
            
            <el-tab-pane v-if="isedit==1" class="eltabpane" label="发货信息" :name="shipping">
                <shipping ref="shipping"></shipping>
            </el-tab-pane>
            <el-tab-pane v-if="isedit==1" class="eltabpane" label="退货信息" :name="returngood">
                <returngood ref="returngood"></returngood>
            </el-tab-pane>
            <el-tab-pane v-if="isedit==1" class="eltabpane" label="服务信息" :name="service">
                <service ref="service"></service>
            </el-tab-pane>
            <el-tab-pane v-if="isedit==1" class="eltabpane" label="回款信息" :name="collection">
                <collection ref="collection"></collection>
            </el-tab-pane>
            <el-tab-pane v-if="isedit==1" class="eltabpane" label="退款信息" :name="refund"><refund ref="refund"></refund></el-tab-pane> 
            <el-tab-pane v-if="isedit==1" class="eltabpane" label="发票信息" :name="invoice">
                <invoice ref="invoice"></invoice>
            </el-tab-pane>
            <el-tab-pane v-if="isedit==1" class="eltabpane" label="操作日志" :name="optionlog">
                <optionlog ref="optionlog"></optionlog>
            </el-tab-pane>
        </el-tabs>
        <div class="return_btn">
            <el-button v-if="activeName=='con_info'" size="mini" type="primary">保存</el-button>
            <el-button @click="returnrouter" size="mini">返回</el-button>
        </div>
    </div>
</template>

<script>
import { reactive, toRefs ,getCurrentInstance,onMounted} from 'vue'
import coninfo from './con_info.vue'
import shipping from './con_shipping.vue'
import returngood from './return_goods.vue'
import collection from './Collection_info.vue'
import refund from './refund.vue'
import service from './Service_information.vue'
import invoice from './Invoice_information.vue'
import optionlog from './Operation_log.vue'
export default {
    components:{coninfo,shipping,returngood,collection,refund,service,invoice,optionlog},
    setup () {
      const { proxy } = getCurrentInstance()
      console.log('11111111111')
        console.log(proxy.$route)
        const state = reactive({
            activeName:'con_info',
            isedit:null
        })
        const handleClick = (tab,event) =>{
            console.log(tab)
            console.log(event)
        }
        const returnrouter = () =>{
            proxy.$router.go(-1)
        }
        onMounted(()=>{
            proxy.isedit = proxy.$route.query.isedit
            proxy.$refs.coninfo.setstatus(proxy.$route.query.isedit)
        })
    
        return {
            handleClick,
            ...toRefs(state),
            returnrouter
        }
    }
}
</script>

<style scoped>
.con_box{
        background: #fff;
        height: 100%;
        position: relative;
    }
    .return_btn{
        position: absolute;
        right: 10px;
        top: 5px;
    }
</style>
<style>
    .eltabs.el-tabs .el-tabs__nav-scroll{
        padding: 0 10px;
        box-sizing: border-box;
    }
    .eltabs.el-tabs .el-tabs__content{
        padding: 0 10px;
        padding-top: 10px;
        box-sizing: border-box;
        background: #fff;
    }
</style>