<template>
  <div>
    <div class="cus_info_box">
      <div
        class="info_box"
        :style="{
          height: $store.state.innerheight - 130 + 'px',
          'overflow-y': 'auto',
        }"
      >
        <div class="basic_info_box">
          <div class="info_three">
            <div class="basic_two">
              <h3>开票计划</h3>
              <div class="basic_one">
                <small>总计划金额：￥5,000.00 ; 总完成金额：￥5,000.00。</small>
              </div>
            </div>

            <div class="btn_right">
              <el-button @click="onSubmit()" type="primary" size="mini"
                >新建</el-button
              >
            </div>
          </div>
          <div class="info_emp_box">
            <el-table
              :height="300"
              size="mini"
              :data="tableData"
              border
              style="width: 100%"
              :header-cell-style="{ background: '#f5f5f5' }"
            >
              <el-table-column prop="Scheduling" label="排期" />
              <el-table-column prop="invoice_title" label="开票计划主题" />
              <el-table-column prop="invoiceplanfee" label="开票计划金额" />
              <el-table-column prop="invoicedate" label="设定开票日期" />
              <el-table-column prop="planstatu" label="计划状态" />
              <el-table-column label="操作" width="200">
                <template #default="scope">
                  <el-button size="mini" type="text">设为完成</el-button>
                  <el-button size="mini" type="text" @click="handedit(scope.row)">编辑</el-button>
                  <el-button size="mini" type="text" style="color: red"
                    >移除</el-button
                  >
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
        <div class="basic_info_box_two">
          <div class="info_three">
            <div class="basic_two">
              <h3>开票记录</h3>
              <div class="basic_one">
                <small>总开票金额：￥5,000.00 。</small>
              </div>
            </div>

            <div class="btn_right">
              <el-button @click="onSubmitone()" type="primary" size="mini"
                >新建</el-button
              >
            </div>
          </div>
          <div class="info_emp_box">
            <el-table
              :height="300"
              size="mini"
              :data="tableDatater"
              border
              style="width: 100%"
              :header-cell-style="{ background: '#f5f5f5' }"
            >
              <el-table-column prop="invoice_title" label="开票主题" />
              <el-table-column prop="invoicefee" label="开票金额" />
              <el-table-column prop="Drawer" label="开票人" />
              <el-table-column prop="invoicedate" label="开票日期" />
              <el-table-column prop="servicetype" label="业务类型" />
              <el-table-column label="操作" width="200">
                <template #default="scope">
                  <el-button size="mini" type="text" @click="handeditone(scope.row)">编辑</el-button>
                  <el-button size="mini" type="text" style="color: red"
                    >移除</el-button
                  >
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
      </div>
    </div>
    <invoicePlan ref="invoicePlan"></invoicePlan>
  </div>
</template>

<script>
import { reactive, toRefs, getCurrentInstance } from "vue";
import invoicePlan from "./invoicePlan.vue";
export default {
  components: {
    invoicePlan,
  },
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      tableData: [
        {
          Scheduling: "ads",
          invoice_title: "dddddd",
          invoiceplanfee: "bbb",
          invoicedate: "kkkk",
          planstatu: "aaa",
        },
        {
          Scheduling: "ads",
          invoice_title: "dddddd",
          invoiceplanfee: "bbb",
          invoicedate: "kkkk",
          planstatu: "aaa",
        },
      ],
      tableDatater: [
        {
          invoice_title: "dcas",
          invoicefee: "vvv",
          Drawer: "pppp",
          invoicedate: "dfff",
          servicetype: "bb",
        },
        {
          nvoice_title: "dcas",
          invoicefee: "vvv",
          Drawer: "pppp",
          invoicedate: "dfff",
          servicetype: "bb",
        },
      ],
    });
    const onSubmit = () => {
      proxy.$refs.invoicePlan.setshow();
    };
    const handedit = (res) => {
      proxy.$refs.invoicePlan.setedit(res);
    };
    const onSubmitone = () => {
      proxy.$router.push({
        path:"/Billing_record",
        query:{
          isedit:0,
          isouter:1
        }
      });
    };
     const handeditone = () => {
      proxy.$router.push({
        path:"/Billing_record",
        query:{
          isedit:1,
          isouter:1
        }
      });
    };
    return {
      ...toRefs(state),
      onSubmit,
      handedit,
      onSubmitone,
      handeditone
    };
  },
};
</script>

<style  scoped>
img {
  width: 20px;
  height: 20px;
}
h3 {
  padding: 0;
  margin: 0;
}
.basic_info_box {
  border: 1px solid #f5f5f5;
  padding: 10px;
  box-sizing: border-box;
}
.basic_info_box_two{
  border: 1px solid #f5f5f5;
  padding: 10px;
  box-sizing: border-box;
  margin-top: 10px;
}
.info_three {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}
.basic_two {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.basic_one {
  margin-left: 30px;
}

.middle {
  display: flex;
  align-items: center;
  margin: 20px 0 10px 0;
}
.middle > small {
  font-size: 16px;
}
.middle_first {
  padding-left: 15px;
  box-sizing: border-box;
}
</style>